\t\t\t\t<div class=\"postMeta\">\n\t\t\t\t\t\t\t\t\t\t\t<div class=\"avatar\"><img class=\"avatar-img\" :src=\"userInfo.avatar_mini\"></div>\n\t\t\t\t\t\t\t\t\t\t\t<div class=\"username\"><a>{{userInfo.username}}</a> in {{userReply.node.title}}</div>\n\t\t\t\t\t\t\t\t\t\t\t<div class=\"created-time\">{{userReply.created | getLastTimeStr true}}</div>\n\t\t\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t\t\t\t<section class=\"postCon\">\n\t\t\t\t\t\t\t\t\t\t\t<h4 class=\"postCon-title\" v-link=\"{ name: 'content',params: { id: userReply.id}}\">{{userReply.title}}</h4>\n\t\t\t\t\t\t\t\t\t\t</section>\n\t\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t\t</div>\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t</div>\n\t</div>\n</template>\n<script>\n\timport loadCon from '../components/loading.vue'\n\timport navCon from '../components/Nav.vue'\n\timport store from '../store'\n\texport default{\n\t\tname:'Member',\n\n\t\tdata(){\n\t\t\treturn{\n\t\t\t\tuserInfo:{},\n\t\t\t\tuserReplys:{},\n\t\t\t\tshowLoad:false\n\t\t\t}\n\t\t},\n\n\t\troute:{\n\t\t\tdata(){\n\t\t\t\tthis.getUserInfo()\n\t\t\t\tthis.getUserReply()\n\t\t\t}\n\t\t},\n\n\t\tmethods:{\n\t\t\t//\n\t\t\tgetUserInfo(){\n\t\t\t\tconst _self = this\n\t\t\t\t_self.showLoad = true\n\t\t\t\tconst params = $.param({username:_self.$route.params.username})\n\t\t\t\tstore.fetchItemsByTag('members/show.json?' + params).then(items => {\n\t\t\t\t    _self.userInfo = items || []\n\t\t\t\t    setTimeout(function(){\n\t\t\t\t        _self.showLoad = false\n\t\t\t\t    }, 1000)\n\t\t\t\t})\n\t\t\t},\n\t\t\t//\n\t\t\tgetUserReply(){\n\t\t\t\tconst _self = this\n\t\t\t\t_self.showLoad = true\n\t\t\t\tconst params = $.param({username:_self.$route.params.username})\n\t\t\t\tstore.fetchItemsByTag('topics/show.json?' + params).then(items => {\n\t\t\t\t    _self.userReplys = items || []\n\t\t\t\t    setTimeout(function(){\n\t\t\t\t        _self.showLoad = false\n\t\t\t\t    }, 1000)\n\t\t\t\t})\n\t\t\t}\n\t\t},\n\n\t\tfilters:{\n\t\t\thasLocation(){\n\t\t\t\treturn this.userInfo.location ?  this.userInfo.location:''\n\t\t\t}\n\t\t},\n\n\t\tcomponents:{\n\t\t\tloadCon,\n\t\t\tnavCon\n\t\t}\n\t}\n</script>\n<style>\n\t.member{\n\t\tmargin-top: 60px;\n\t}\n\t.member-tagline{\n\t\tcolor: rgba(0,0,0,0.6);\n\t\tfont-size: 18px;\n\t\toutline: 0;\n\t\tword-break: break-word;\n\t\tword-wrap: break-word;\n\t}\n\t.postMeta div{\n\t\tdisplay: table-cell;\n\t\tpadding-right: 5px;\n\t}\n\t.member-reply{\n\t\tmargin-top: 20px;\n\t}\n\t.avatar-big{\n\t\tfloat: right;\n\t}\n\t.post-head {\n\t    margin: 10px 0;\n\t    padding-bottom: 5px;\n\t    border-bottom: 1px solid #eee;\n\t}\n\t.post-item {\n\t    margin: 10px 0;\n\t    padding-bottom: 10px;\n\t    border-bottom: 1px solid #eee;\n\t}\n\n</style>\n"],"sourceRoot":"webpack://"}]);

