<template>
	<nav-con :title="''"></nav-con>
	<div class="member">
		<div class="container">
			<div class="row">
				<div class="col-md-6">
					<load-con :show="showLoad"></load-con>
					<div class="member-stauts" v-show="!showLoad">
						<ul>
							<li class="avatar-big"><img class="avatar-img" :src="userInfo.avatar_large"></li>
							<li class="member-username"><h2>{{userInfo.username}}</h2></li>
							<li class="member-create_time"><span class="glyphicon glyphicon-time">{{userInfo.created | getLastTimeStr false}}</span></li>
							<li class="member-location"><span class="glyphicon glyphicon-map-marker">{{userInfo.location | hasLocation}}</span></li>
							<li class="member-tagline">{{userInfo.tagline}}</li>
						</ul>
					</div>
					<div class="member-post-list" v-show="!showLoad">
						<div class="container">
							<div class="row">
								<div class="col-md-6">
									<div class="post-head">
										<strong></strong>
									</div>
									<div class="post-item" v-for="userReply in userReplys">
										<div class="postMeta">
											<div class="avatar"><img class="avatar-img" :src="userInfo.avatar_mini"></div>
											<div class="username"><a>{{userInfo.username}}</a> in {{userReply.node.title}}</div>
											<div class="created-time">{{userReply.created | getLastTimeStr true}}</div>
										</div>
										<section class="postCon">
											<h4 class="postCon-title" v-link="{ name: 'content',params: { id: userReply.id}}">{{userReply.title}}</h4>
										</section>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import loadCon from '../components/loading.vue'
	import navCon from '../components/Nav.vue'
	import store from '../store'
	export default{
		name:'Member',

		data(){
			return{
				userInfo:{},
				userReplys:{},
				showLoad:false
			}
		},

		route:{
			data(){
				this.getUserInfo()
				this.getUserReply()
			}
		},

		methods:{
			//
			getUserInfo(){
				const _self = this
				_self.showLoad = true
				const params = $.param({username:_self.$route.params.username})
				store.fetchItemsByTag('members/show.json?' + params).then(items => {
				    _self.userInfo = items || []
				    setTimeout(function(){
				        _self.showLoad = false
				    }, 1000)
				})
			},
			//
			getUserReply(){
				const _self = this
				_self.showLoad = true
				const params = $.param({username:_self.$route.params.username})
				store.fetchItemsByTag('topics/show.json?' + params).then(items => {
				    _self.userReplys = items || []
				    setTimeout(function(){
				        _self.showLoad = false
				    }, 1000)
				})
			}
		},

		filters:{
			hasLocation(){
				return this.userInfo.location ?  this.userInfo.location:''
			}
		},

		components:{
			loadCon,
			navCon
		}
	}
</script>
<style>
	.member{
		margin-top: 60px;
	}
	.member-tagline{
		color: rgba(0,0,0,0.6);
		font-size: 18px;
		outline: 0;
		word-break: break-word;
		word-wrap: break-word;
	}
	.postMeta div{
		display: table-cell;
		padding-right: 5px;
	}
	.member-reply{
		margin-top: 20px;
	}
	.avatar-big{
		float: right;
	}
	.post-head {
	    margin: 10px 0;
	    padding-bottom: 5px;
	    border-bottom: 1px solid #eee;
	}
	.post-item {
	    margin: 10px 0;
	    padding-bottom: 10px;
	    border-bottom: 1px solid #eee;
	}

</style>



