<template>
    <nav-con :title="nodeName"></nav-con>
    <div class="wrap">
			<div class="container">
				<div class="row">
					<div class="col-xs-12 col-md-9">
						<load-con :show="showLoad"></load-con>
		        <item-con :show="!showLoad" v-ref:listItem></item-con>
					</div>
				</div>
			</div>
    </div>
  </div>
</template>

<script>
import itemCon from '../components/Item.vue'
import loadCon from '../components/loading.vue'
import navCon from '../components/Nav.vue'
import store from '../store'

export default {

  name: 'List',

  data(){
  	return{
  		navActive:true,
  		showLoad:false,
      nodeName:''
  	}
  },

  components:{
    itemCon,
    loadCon,
    navCon
  },

  ready(){
  	this.getLatest()
  },


  methods:{
  	//
  	getLatest (){
  		const _self = this
      _self.showLoad = true
      _self.nodeName = ""
      store.fetchItemsByTag('topics/latest.json').then(items => {
          _self.$refs.listitem.items = items || []
          setTimeout(function(){
              _self.showLoad = false
          }, 1000)
      })
  	},
  	//
  	getHot(){
	    const _self = this
      _self.showLoad = true
      _self.nodeName = ""
      store.fetchItemsByTag('topics/hot.json').then(items => {
          _self.$refs.listitem.items = items || []
          setTimeout(function(){
              _self.showLoad = false
          }, 1000)
      })
  	},
    //
    getListByNode(nodeid,nodename){
        const _self = this
        const params = $.param({node_id:nodeid})
        _self.nodeName = nodename
        _self.showLoad = true
        store.fetchItemsByTag('topics/show.json?' + params).then(items => {
          _self.$refs.listitem.items = items || []
          setTimeout(function(){
              _self.showLoad = false
          }, 1000)
      })
    }
  }

}
</script>
<style>
	.wrap{
		height: auto;
    margin-top: 60px;
	}
	.nav-tabs{
		display: flex;
	}
	.nav>li{
		flex-grow:1;
		justify-content:center;
	}
	.nav>li>span{
		padding: 5px;
		font-size: 16px;
	}

</style>



